import React, { FC } from "react";

import {
	MenuBarWrapper,
	 StyledMenuChildCenter,
	 StyledMenuChildLeft,
	 StyledMenuChildrenWrapper,
	 StyledMenuChildRight,
} from "./MenuBar.styles";
import { MenuBarProps } from "./MenuBar.types";

const MenuBar: FC<MenuBarProps> = ({
	leftActions,
	middleActions,
	rightActions,
	dataQA,
	className,
}) => (
	<MenuBarWrapper className={className} data-qa={dataQA}>
		<StyledMenuChildrenWrapper data-qa="menu-bar-children">
			<StyledMenuChildLeft>
				{leftActions}
			</StyledMenuChildLeft>
			{middleActions && (
				<StyledMenuChildCenter>
					{middleActions}
				</StyledMenuChildCenter>
			)}
			<StyledMenuChildRight>
				{rightActions}
			</StyledMenuChildRight>
		</StyledMenuChildrenWrapper>
	</MenuBarWrapper>
);

MenuBar.displayName = "MenuBar";
export default MenuBar;

